import { action, computed, observable, reaction } from 'mobx'
import { calculateGridSize, getWindowSize, I2DVector, makeOrderedSpiral } from '../helpers/grid'
import { getRandomEmoji } from '../helpers/emoji'
import uuid from 'uuid'
import { debounce } from 'lodash'
import { delay } from '../helpers/scenario'

export enum EViews { initial, application, snake }

export type TFlipperContent = string | null

export interface IFlipper {
  content: TFlipperContent[]
  delay: number
  vector: I2DVector
  id: string
  withAnimation: boolean
}

export class GridService {
  @observable
  grid: IFlipper[][] = []

  @observable
  columns: number = 0

  @observable
  rows: number = 0

  @observable.struct windowSize = getWindowSize()

  @observable
  view: EViews = EViews.initial

  init() {
    console.log('init')

    const { columns, rows } = calculateGridSize(this.windowSize)

    this.columns = columns
    this.rows = rows

    if (this.needToShowInitialAnimation) {
      this.showInitialAnimation()
    }
  }

  get needToShowInitialAnimation() {
    return true
  }

  @computed
  get square() {
    return this.columns * this.rows
  }

  @action
  async showInitialAnimation() {
    const
      spiral = makeOrderedSpiral(this.rows, this.columns),
      LINEAR_DELAY = 20,
      INITIAL_DELAY = 200,
      spiralGrid = spiral.map(
        row => row.map(
          ({ value, vector }) => ({
            id: uuid.v4(),
            content: [null, getRandomEmoji()],
            delay: INITIAL_DELAY + LINEAR_DELAY * value,
            withAnimation: false,
            vector,
          })
        )
      )

    this.grid = spiralGrid

    await (delay(INITIAL_DELAY + this.square * LINEAR_DELAY + 400))

    const
      middleX = Math.floor(this.columns / 2),
      middleY = Math.floor(this.rows / 2),
      next = spiralGrid.map((row, y) => row.map(
        (flipper, x) => ({
          ...flipper,
          delay: 1000 + Math.max(Math.abs(middleY - y), Math.abs(middleX - x)) * 130,
          vector: Math.abs(middleY - y) === Math.abs(middleX - x) ? {
            x: x < middleX ? -1 : 1,
            y: y < middleY ? -1 : 1
          } : x < middleX ? {
            x: -1,
            y: 0
          } : {
            x: 1,
            y: 0
          },
          content: [flipper.content[1], null]
        })
      ))

    this.grid = next
  }

  constructor() {
    this.init()

    window.addEventListener('resize', debounce(() => {
      console.log('resize')
      this.windowSize = getWindowSize()
    }, 500))

    reaction(() => {
      return this.windowSize
    }, () => {
      this.init()
    })
  }
}


const makeGrid = () => {
  return new GridService()
}

export default makeGrid
