import React from 'react'
import classNames from 'classnames'
import styles from './Flipper.module.scss'
import { getFlipDirectionKey } from '../../helpers/grid'
import { IFlipper } from '../../services/GridService'

interface IState {
  flip: boolean
}

export default class Flipper extends React.Component<IFlipper, IState> {
  timeout: any

  constructor(props: IFlipper) {
    super(props)
    this.state = {
      flip: false,
    }

    this.timeout = setTimeout(() => {
      this.flip()
    }, props.delay)
  }

  componentWillUnmount(): void {
    clearTimeout(this.timeout)
  }

  flip = () => {
    this.setState(prevState => ({
      ...prevState,
      flip: !prevState.flip
    }))
  }

  get flipDirectionClass() {
    const { vector } = this.props

    return `flipDirection${getFlipDirectionKey(vector)}`
  }

  render() {
    const
      { flip } = this.state,
      { content, withAnimation } = this.props,
      side1Content = content[0],
      side2Content = content[1]

    return withAnimation ? (
      <div className={classNames(styles.flipper, styles[this.flipDirectionClass], flip && styles.isFlipped)}>
        <div className={styles.flipperIn}>
          <div className={styles.flipperSide}>
            {side1Content}
          </div>

          <div className={styles.flipperSide}>
            {side2Content}
          </div>
        </div>
      </div>
    ) : (
      <div className={classNames(styles.flipper, styles.withoutAnimation)}>
        <div className={styles.flipperIn}>
          <div className={styles.flipperSide}>
            {flip ? side2Content : side1Content}
          </div>
        </div>
      </div>
    )
  }
}
